export default "<template>\r\n    <div class=\"MonHocToan\">\r\n        <raw-loader :mytxt=\"require('!raw-loader!./NguoiDung.vue').default\"></raw-loader>\r\n        <h1>User List</h1>\r\n        <div>\r\n            <form @submit.stop.prevent=\"onSave\">\r\n                <b-table thead-class=\"myTable\" ref=\"mytable\" :items=\"myProvider\" :fields=\"fields\"\r\n                    :current-page=\"currentPage\" :per-page=\"perPage\" :filter=\"filter\" :filter-included-fields=\"filterOn\"\r\n                    :sort-by.sync=\"sortBy\" :sort-desc.sync=\"sortDesc\" :sort-direction=\"sortDirection\" stacked=\"sm\"\r\n                    show-empty small fixed outlined bordered @filtered=\"onFiltered\">\r\n                    <template v-slot:cell()=\"{ value, item, field: { key } }\">\r\n                        <template v-if=\"sel[pri] != item[pri]\">\r\n                            <template v-if=\"item[pri] != 0\">\r\n                                <template v-if=\"key == pri\">\r\n                                    {{ value }}\r\n                                </template>\r\n                                <template v-if=\"key == 'FirstName'\">\r\n                                    {{ value }}\r\n                                </template>\r\n                                <template v-if=\"key == 'LastName'\">\r\n                                    {{ value }}\r\n                                </template>\r\n                                <template v-if=\"key == 'Username'\">\r\n                                    {{ value }}\r\n                                </template>\r\n                                <template v-if=\"key == 'Role'\">\r\n                                    <v-select :disabled=\"true\" multiple v-model=\"item[key]\" v-validate=\"'required'\" :name=\"key\"\r\n                                    :reduce=\"(option) => option.id\" :options=\"\r\n                                        roles\r\n                                    \" :has-error=\"errors.has(key)\" :class=\"{ danger: errors.has(key) }\" />\r\n                                </template>\r\n                                <template v-if=\"key == 'TrangThai'\">\r\n                                    <b-form-checkbox :disabled=\"true\" size=\"sm\" class=\"my-checkbox\" v-model=\"item[key]\" \r\n                                        :name=\"key\"\r\n                                        value=true\r\n                                        unchecked-value=false>\r\n                                    </b-form-checkbox>\r\n                                </template>\r\n                            </template>\r\n                        </template>\r\n                        <template v-if=\"(sel[pri] == item[pri] || item[pri] == 0)\">\r\n                            <template v-if=\"key == 'Id'\">\r\n                                <span>{{ value }}</span>\r\n                            </template>\r\n                            <template v-if=\"key == 'FirstName'\">\r\n                                <b-form-input v-model=\"item[key]\" :name=\"key\" v-validate=\"'required'\" placeholder=\"\"\r\n                                    :class=\"{ 'ko-hop-le': errors.has(key) }\">\r\n                                </b-form-input>\r\n                            </template>\r\n                            <template v-if=\"key == 'LastName'\">\r\n                                <b-form-input v-model=\"item[key]\" :name=\"key\" v-validate=\"'required'\" placeholder=\"\"\r\n                                    :class=\"{ 'ko-hop-le': errors.has(key) }\">\r\n                                </b-form-input>\r\n                            </template>\r\n                            <template v-if=\"key == 'Username'\">\r\n                                <b-form-input v-model=\"item[key]\" :name=\"key\" v-validate=\"'required'\" placeholder=\"\"\r\n                                    :class=\"{ 'ko-hop-le': errors.has(key) }\">\r\n                                </b-form-input>\r\n                            </template>\r\n                            <template v-if=\"key == 'Role'\">\r\n                                <v-select multiple v-model=\"item[key]\" v-validate=\"'required'\" :name=\"key\"\r\n                                    :reduce=\"(option) => option.id\" :options=\"\r\n                                        roles\r\n                                    \" :has-error=\"errors.has(key)\" :class=\"{ danger: errors.has(key) }\" />\r\n                            </template>\r\n                            <template v-if=\"key == 'TrangThai'\">\r\n                                <b-form-checkbox size=\"sm\" class=\"my-checkbox\" v-model=\"item[key]\" \r\n                                    :name=\"key\"\r\n                                    value=true\r\n                                    unchecked-value=false>\r\n                                </b-form-checkbox>\r\n                            </template>\r\n\r\n\r\n                        </template>\r\n                    </template>\r\n                    <template #cell(cmd)=\"row\">\r\n                        <template v-if=\"row.item[pri] == 0\">\r\n                            <b-button class=\"mr-1\" size=\"sm\" @click=\"onSave(row.item)\">\r\n                                <b-icon-hand-thumbs-up></b-icon-hand-thumbs-up>\r\n                            </b-button>\r\n                            <b-button class=\"mr-1\" size=\"sm\" @click=\"OnCancel(row.item)\">\r\n                                <b-icon-file-x></b-icon-file-x>\r\n                            </b-button>\r\n                        </template>\r\n                        <template v-else>\r\n                            <b-button :disabled=\"adding == true || isEditing() == true\" variant=\"outline\" class=\"mr-1\"\r\n                                v-if=\"sel[pri] != row.item[pri]\" size=\"sm\" @click=\"OnEdit(row.item)\">\r\n                                <b-icon-pencil-square></b-icon-pencil-square>\r\n                            </b-button>\r\n                            <b-button :disabled=\"adding == true || isEditing() == true\" variant=\"outline\" class=\"mr-1\"\r\n                                v-if=\"sel[pri] != row.item[pri]\" size=\"sm\" @click=\"OnDel(row.item)\">\r\n                                <b-icon-trash></b-icon-trash>\r\n                            </b-button>\r\n                            <b-button variant=\"outline\" class=\"mr-1\" v-if=\"sel[pri] == row.item[pri]\" size=\"sm\"\r\n                                @click=\"onSave(row.item)\">\r\n                                <b-icon-hand-thumbs-up></b-icon-hand-thumbs-up>\r\n                            </b-button>\r\n                            <b-button variant=\"outline\" class=\"mr-1\" v-if=\"sel[pri] == row.item[pri]\" size=\"sm\"\r\n                                @click=\"OnCancel(row.item)\">\r\n                                <b-icon-file-x></b-icon-file-x>\r\n                            </b-button>\r\n                        </template>\r\n                    </template>\r\n\r\n                    <template #bottom-row=\"data\">\r\n                        <td :colspan=\"data.columns\">\r\n                            <b-row>\r\n                                <b-col sm=\"6\" class=\"my-1\">\r\n                                    <b-form-group label=\"Tìm kiếm\" label-for=\"filter-input\" label-cols-sm=\"3\"\r\n                                        label-align-sm=\"right\" label-size=\"sm\" class=\"mb-0\">\r\n                                        <b-input-group size=\"sm\">\r\n                                            <b-form-input id=\"filter-input\" v-model=\"filter\" type=\"search\"\r\n                                                placeholder=\"Type to Search\">\r\n                                            </b-form-input>\r\n\r\n                                            <b-input-group-append>\r\n                                                <b-button :disabled=\"!filter\" @click=\"filter = ''\">Clear</b-button>\r\n                                            </b-input-group-append>\r\n                                        </b-input-group>\r\n                                    </b-form-group>\r\n                                </b-col>\r\n                                <b-col sm=\"6\" class=\"my-1\">\r\n                                    <b-form-group label=\"Số dòng\" label-for=\"per-page-select\" label-cols-sm=\"6\"\r\n                                        label-cols-md=\"4\" label-cols-lg=\"3\" label-align-sm=\"right\" label-size=\"sm\"\r\n                                        class=\"mb-0\">\r\n                                        <b-form-select id=\"per-page-select\" v-model=\"perPage\" :options=\"pageOptions\"\r\n                                            size=\"sm\">\r\n                                        </b-form-select>\r\n                                    </b-form-group>\r\n                                </b-col>\r\n\r\n                                <b-col sm=\"6\" class=\"my-1 text-right\">\r\n                                    <span>page : {{ currentPage }} / {{ last_page }} of {{ totalRows }} </span>\r\n                                    <b-button :disabled=\"adding == true || isEditing() == true\" class=\"mr-1 mb-1\" size=\"sm\"\r\n                                        @click=\"OnAdd\">\r\n                                        <b>Add</b></b-button>\r\n                                </b-col>\r\n                                <b-col sm=\"6\" class=\"my-1\">\r\n                                    <b-pagination :disabled=\"adding == true || isEditing() == true\" ref=\"mypage\"\r\n                                        v-model=\"currentPage\" :total-rows=\"totalRows\" :per-page=\"perPage\" size=\"sm\"\r\n                                        align=\"fill\" @change=\"onChangePage\" @input=\"onPagingInput\" first-text=\"First\"\r\n                                        prev-text=\"Prev\" next-text=\"Next\" last-text=\"Last\">\r\n                                    </b-pagination>\r\n                                </b-col>\r\n                            </b-row>\r\n\r\n                        </td>\r\n                    </template>\r\n\r\n                    <template #row-details=\"row\">\r\n                        <b-card>\r\n                            <ul>\r\n                                <li v-for=\"(value, key) in row.item\" :key=\"key\">{{ key }}: {{ value }}</li>\r\n                            </ul>\r\n                        </b-card>\r\n                    </template>\r\n                </b-table>\r\n\r\n            </form>\r\n            <!-- Info modal -->\r\n            <b-modal :id=\"infoModal.id\" :title=\"infoModal.title\" ok-only @hide=\"resetInfoModal\">\r\n                <pre>{{ infoModal.content }}</pre>\r\n            </b-modal>\r\n        </div>\r\n    </div>\r\n</template>\r\n<script>\r\n\r\nimport VeeValidate from \"vee-validate\";\r\nimport Vue from \"vue\";\r\nVue.use(VeeValidate, {\r\n    classes: true,\r\n    fieldsBagName: 'veeFields'\r\n});\r\nimport vSelect from \"vue-select\";\r\nVue.component(\"v-select\", vSelect);\r\n\r\nimport VueTypeaheadBootstrap from \"vue-typeahead-bootstrap\";\r\nimport { debounce } from \"lodash\";\r\n\r\nimport fetchClient from \"@/fetchClient\";\r\nimport moment from \"moment\";\r\nimport TumLum from \"@/TumLum\";\r\nexport default {\r\n    components: {\r\n        VueTypeaheadBootstrap\r\n    },\r\n\r\n    data() {\r\n        return {\r\n            items: [\r\n            ],\r\n            fields: [\r\n                {\r\n                    key: 'Id',\r\n                    formatter: (value, key, item) => {\r\n                        return value;\r\n                    },\r\n                    sortable: true,\r\n                    thClass: 'text-center',\r\n                    tdClass: 'text-center align-middle',\r\n                    thStyle: { width: \"8%\" },\r\n                },\r\n                //{ key: 'TenHocSinh', sortable: true, thClass: 'text-center', tdClass: 'align-middle', thStyle: { width: \"80%\" }, },\r\n                { key: 'FirstName', sortable: true, thClass: 'text-center', tdClass: 'align-middle', thStyle: { width: \"10%\" }, },\r\n                { key: 'LastName', sortable: true, thClass: 'text-center', tdClass: 'align-middle', thStyle: { width: \"10%\" }, },\r\n                { key: 'Username', sortable: true, thClass: 'text-center', tdClass: 'align-middle', thStyle: { width: \"10%\" }, },\r\n                { key: 'Role', sortable: true, thClass: 'text-center', tdClass: 'align-middle', thStyle: { width: \"45%\" }, },\r\n                { key: 'TrangThai', label: \"TT\", sortable: true, thClass: 'text-center', tdClass: 'align-middle text-center', thStyle: { width: \"5%\" }, },\r\n\r\n                {\r\n                    key: 'cmd',\r\n                    class: 'text-center',\r\n                    thClass: 'text-center',\r\n                    tdClass: 'align-middle',\r\n                    thStyle: { width: \"12%\" }\r\n                }\r\n            ],\r\n            totalRows: 1,\r\n            currentPage: 1,\r\n            perPage: 5,\r\n            pageOptions: [5, 10, 15, { value: 100, text: \"Show a lot\" }],\r\n            sortBy: '',\r\n            sortDesc: false,\r\n            sortDirection: 'asc',\r\n            filter: null,\r\n            filterOn: [],\r\n            infoModal: {\r\n                id: 'info-modal',\r\n                title: '',\r\n                content: ''\r\n            },\r\n            sel: {},\r\n            pri: \"Id\",\r\n            last_page: 0,\r\n            adding: false,\r\n            ListAuto: [],\r\n            Lops: [],\r\n\r\n            url: `/api/NguoiDung5`,\r\n            roles: [],\r\n        }\r\n    },\r\n\r\n    watch: {\r\n        last_page: function (newValue, oldValue) {\r\n        },\r\n        currentPage: function (val) {\r\n        },\r\n        totalRows: function (newValue, oldValue) {\r\n        }\r\n    },\r\n    computed: {\r\n    },\r\n    mounted() {\r\n    },\r\n    methods: {\r\n        OnSearch: debounce(function (query) {\r\n            fetch(`/api/Ban/AutoComplete?q=${query}`)\r\n                .then((response) => {\r\n                    return response.json();\r\n                })\r\n                .then((data) => {\r\n                    console.log(JSON.stringify(data))\r\n                    this.ListAuto = data.items;\r\n                });\r\n        }, 500),\r\n        onPagingInput() {\r\n        },\r\n        myProvider(ctx) {\r\n            let currentPage = ctx.currentPage;\r\n            let perPage = ctx.perPage;\r\n            let sortBy = ctx.sortBy;\r\n            let sortDesc = ctx.sortDesc;\r\n            let vm = this;\r\n            let url = `${this.url}/GetPage2`;\r\n            url = `${url}?page=${currentPage}&size=${perPage}&sortBy=${sortBy}&sortDesc=${sortDesc}`;\r\n            if (ctx.filter) {\r\n                let filter = ctx.filter;\r\n                url = `${url}&filter=${filter}`;\r\n            }\r\n            return fetchClient.get(url)\r\n                .then(res => {\r\n                    this.LdsRoleComboItem();\r\n                    //this.LdsLop();\r\n                    //alert(JSON.stringify(res));\r\n\r\n                    /*{\"data\":{\"last_page\":1,\"rowCount\":5,\r\n                        \"data\":[{\r\n                            \"Id\":1,\r\n                            \"FirstName\":\"Tấn Thức\",\r\n                            \"LastName\":\"Nguyễn\",\r\n                            \"Username\":\"admin\",\r\n                            \"Password\":\"21232f297a57a5a743894a0e4a801fc3\",\r\n                            \"Role\":\"Admin,User\",\r\n                            \"Token\":null,\r\n                            \"TrangThai\":true\r\n                    */\r\n                    vm.last_page = res.data.last_page;\r\n                    vm.totalRows = res.data.rowCount;\r\n                    if (this.adding == true) {\r\n                        /*\r\n                        let r2={}\r\n                        r2[this.pri]=0;\r\n                        this.fields.forEach(element => {\r\n                            if(element.key!='cmd' && element.key!=this.pri) r2[element.key]=\"xyz\"\r\n                        });\r\n                        alert(JSON.stringify(r2));\r\n                        {\"Id\":0,\"TenHocSinh\":\"xyz\",\"NamSinh\":\"xyz\",\"LopHocId\":\"xyz\"}\r\n                        */\r\n                        let r = {};\r\n                        r[this.pri] = 0;\r\n                        r[\"FirstName\"] = \"first name\";\r\n                        r[\"LastName\"] = \"last name\";\r\n                        r[\"Username\"] = \"user name\";\r\n                        r[\"Password\"] = \"passworld\";\r\n                        r[\"Role\"] = \"role\";\r\n                        r[\"Token\"] = \"token\";\r\n                        r[\"TrangThai\"] = 1;\r\n                        res.data.data.unshift(r);\r\n                    }\r\n                    return vm.mapData(res.data.data); //res.data.data;\r\n                })\r\n        },\r\n        mapData(data) {\r\n            let kq = [];\r\n            data.forEach(obj => {\r\n                let x = {};\r\n                Object.keys(obj).forEach(key => {\r\n                    let k = key;\r\n                    if (k != \"Role\") {\r\n                        x[k] = obj[key];\r\n                    } else {\r\n                        if (obj[key] == \"\") {\r\n                            x[k] = null;\r\n                        } else {\r\n                            x[k] = obj[key].split(\",\");\r\n                            //x[k]=\"xyz\";\r\n                        }\r\n                    }\r\n                });\r\n                kq.push(x);\r\n            })\r\n            return kq;\r\n        },\r\n        onChangePage() {\r\n            this.sel = {};\r\n            this.adding = false;\r\n        },\r\n        OnAdd() {\r\n            this.adding = true;\r\n            this.$refs.mytable.refresh();\r\n        },\r\n        OnEdit(item) {\r\n            if (JSON.stringify(this.sel) != \"{}\" || this.adding == true) {\r\n                return;\r\n            }\r\n            //this.sel = JSON.parse(JSON.stringify(item));\r\n            this.getById(item[this.pri]);\r\n\r\n            return;\r\n            this.getById(item[this.pri]).then(res => {\r\n                this.sel = res.data;\r\n            })\r\n        },\r\n        onSave(item) {\r\n            this.$validator.validateAll().then((result) => {\r\n                if (!result) {\r\n                    this.toast(\"Không hợp lệ\");\r\n                    return;\r\n                }\r\n                let r = JSON.parse(JSON.stringify(item));\r\n                let roleStr = \"\";\r\n                r.Role.forEach((v, k) => {\r\n                    if (k < r.Role.length - 1) {\r\n                        roleStr += `${v},`;\r\n                    } else {\r\n                        roleStr += `${v}`;\r\n                    }\r\n                })\r\n                r.Role = roleStr;\r\n                if (r[this.pri] == 0) {\r\n                    this.Add(r);\r\n                } else {\r\n                    this.Update(r);\r\n                }\r\n            });\r\n        },\r\n        OnCancel(item) {\r\n            if (item[this.pri] != 0) {\r\n                this.doCancel(item);\r\n                this.sel = {};\r\n            } else {\r\n                this.adding = false;\r\n                this.$refs.mytable.refresh();\r\n            }\r\n        },\r\n        OnDel(item) {\r\n            this.Del(item[this.pri]);\r\n        },\r\n        delRow(item) {\r\n            let id = item.id;\r\n            let index = this.items.findIndex(x => x.id == id);\r\n            this.items.splice(index, 1);\r\n        },\r\n        isEditing() {\r\n            return JSON.stringify(this.sel) != \"{}\"\r\n        },\r\n        isAdded() {\r\n            let kq = false;\r\n            this.items.forEach(x => {\r\n                if (x.id == 0) kq = true;\r\n            })\r\n            return kq;\r\n        },\r\n        doCancel(item) {\r\n            for (let key in item) {\r\n                if (item[key] != this.sel[key]) {\r\n                    item[key] = this.sel[key]\r\n                }\r\n            }\r\n        },\r\n        getLastPage(count) {\r\n            let pageSize = this.perPage;//this.tabulator.getPageSize();\r\n            let du = count % pageSize;\r\n            return du == 0 ? count / pageSize : (count - du) / pageSize + 1;\r\n        },\r\n        gotoPage(page) {\r\n            this.currentPage = page;\r\n            this.$refs.mytable.refresh();\r\n        },\r\n        toast(msg, variant = null) {\r\n            this.$bvToast.toast(msg, {\r\n                title: `Information`,\r\n                variant: variant,\r\n                solid: true,\r\n            });\r\n        },\r\n        async Add(r) {\r\n            return await fetchClient.post(`${this.url}/Add`, r)\r\n                .then(res => {\r\n                    this.toast(`${res.data.msg}`);\r\n                    this.adding = false;\r\n                    this.$refs.mytable.refresh();\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async Update(r) {\r\n            return await fetchClient.post(`${this.url}/Update`, r)\r\n                .then(res => {\r\n                    this.toast(`${res.data.msg}`);\r\n                    this.adding = false;\r\n                    this.sel = {};\r\n                    this.$refs.mytable.refresh();\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async Del(id) {\r\n            return await fetchClient.get(`${this.url}/Delete?id=${id}`)\r\n                .then(res => {\r\n                    if (res.data.kq == 'nook') {\r\n                        this.toast(`${res.data.msg}`);\r\n                        return;\r\n                    }\r\n                    this.toast(`${res.data.msg}`);\r\n                    let lastPage = this.getLastPage(this.totalRows - 1);\r\n                    if (lastPage < this.currentPage) this.currentPage = lastPage;\r\n                    this.$refs.mytable.refresh();\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async getById(id) {\r\n            return await fetchClient.get(`${this.url}/GetById?id=${id}`)\r\n                .then(res => {\r\n                    this.sel = res.data\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async LdsLop() {\r\n            return await fetchClient.get(`/api/LopHoc4/LdsComboItem`)\r\n                .then(res => {\r\n                    this.Lops = res.data.list;\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async LdsRoleComboItem() {\r\n            return await fetchClient.get(`/api/NDRole5/LdsComboItem`)\r\n                .then(res => {\r\n                    this.roles = res.data.list;\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        //LdsComboItem\r\n        info(item, index, button) {\r\n            this.infoModal.title = `Row index: ${index}`\r\n            this.infoModal.content = JSON.stringify(item, null, 2)\r\n            this.$root.$emit('bv::show::modal', this.infoModal.id, button)\r\n        },\r\n        resetInfoModal() {\r\n            this.infoModal.title = ''\r\n            this.infoModal.content = ''\r\n        },\r\n        onFiltered(filteredItems) {\r\n            this.totalRows = filteredItems.length\r\n            this.currentPage = 1\r\n        }\r\n    }\r\n}\r\n</script>";